.showOverlay {
  bottom: 0;
  display: flex;
  font-size: 16px;
  left: calc(50% - 55px);
  margin-bottom: 7px;
  position: absolute;
}
.check {
  bottom: 0;
  display: flex;
  left: calc(50% + 40px);
  margin-bottom: 10px;
  position: absolute;
}
.containerOuter {
  background: black;
  border-radius: 8px;
  box-shadow: 2px 6px 20px 2px rgba(10,10,120,0.15);
}
.container1 {
  position: relative;
  margin: 20px;
  overflow: hidden;
  width: 160px;
}
.hidden {
  display: none;
}
.entry {
  height: 25px;
  position: absolute;
  width: 160px;
}
.entry:nth-child(2) {
  left: 8px;
  top: 8px;
}
.entry:nth-child(4) {
  left: 8px;
  top: 58px;
}
.entry:nth-child(6) {
  left: 8px;
  top: 108px;
}
.entry:nth-child(8) {
  left: 8px;
  top: 158px;
}
.entry:nth-child(10) {
  left: 8px;
  top: 208px;
}
.circle {
  border: 2px solid #545556;
  border-radius: 50%;
  cursor: pointer;
  height: 20px;
  position: absolute;
  transition: border-color 300ms;
  width: 20px;
}
.entry-label {
  cursor: pointer;
  margin-top: -3px;
  padding-left: 40px;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  -moz-user-select: none;
}
.overlay {
  /*background: white;*/
  -webkit-mask: url(#holes);
          mask: url(#holes);
  -webkit-mask-image: url(data:image/png;base64,);
  height: 240px;
  pointer-events: none;
  transition: background 300ms;
  width: 40px;
}
.highlight {
  background: white;
  border-radius: 50%;
  height: 12px;
  left: 14px;
  pointer-events: none;
  position: absolute;
  top: 14px;
  transition: transform 400ms cubic-bezier(0.175, 0.885, 0.32, 1.2);
  transform: translateY(-50px);
  width: 12px;
}
.hidden:nth-child(1):checked ~ .highlight {
  transform: translateY(0);
}
.hidden:nth-child(3):checked ~ .highlight {
  transform: translateY(50px);
}
.hidden:nth-child(5):checked ~ .highlight {
  transform: translateY(100px);
}
.hidden:nth-child(7):checked ~ .highlight {
  transform: translateY(150px);
}
.hidden:nth-child(9):checked ~ .highlight {
  transform: translateY(200px);
}
.hidden:nth-child(1):checked + .entry .circle {
  border-color: white;
}
.hidden:nth-child(3):checked + .entry .circle {
  border-color: white;
}
.hidden:nth-child(5):checked + .entry .circle {
  border-color: white;
}
.hidden:nth-child(7):checked + .entry .circle {
  border-color: white;
}
.hidden:nth-child(9):checked + .entry .circle {
  border-color: white;
}
.check:checked ~ .containerOuter .overlay {
  background: #ecf0f4;
}
